<template>
  <div class = "container">
    <div class = "currency">
      <h2>{{curRates.base}} on {{curRates.date}}</h2>
      <br>
      <form>
        <label>Change base currency</label>
        <select v-model="baseCurrency">
          <option v-for="(rate, currency) in curRates.rates">{{ currency }}</option>
        </select>
        <button v-on:click.prevent="selectBaseCurrency">Change</button>
      </form>
    </div>
    <hr>
    <br>
    <div class="rates">
      <p v-for="(rate, currency) in curRates.rates">{{currency}} = {{1/rate}} {{curRates.base}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CurrencyRates',
  data () {
    return {
      curRates: [],
      baseurl: 'https://api.fixer.io/latest?base=',
      baseCurrency: 'INR',
      url: 'https://api.fixer.io/latest?base=INR',
      currencies: []
    }
  },
  methods: {
    selectBaseCurrency: function(){
      this.url = this.baseurl+this.baseCurrency;
      this.$http.get(this.url).then(function(data){
        console.log(data);
        this.curRates = data.body
      });
    }
  },
  created() {
    this.$http.get(this.url).then(function(data){
      console.log(data);
      this.curRates = data.body
    });
  }
  // updated() {
  //   this.$http.get('https://api.fixer.io/latest?base=INR').then(function(data){
  //     console.log(data);
  //     this.curRates = data.body
  //   });
  // }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.container{
  background: lavender;
  width: 70%;
  margin: 0 auto;
  padding: 10px 5px 10px 5px;
  border-radius: 12px;
}
.currency *{
  display: inline;
}
.container hr{
  clear: both;
}
.currency form{
  float: right;
}
select{
  width: 100px;
}
</style>



