<template>
  <div>
    <h1>{{title}}</h1>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      title: 'Currency Rates'
    }
  },
  methods: {
    }
  }
</script>

<style scoped>
  div{
    background-color: lightgreen;
    width: 70%;
    margin: 0 auto;
    padding: 0px 5px 0px 5px;
    border-radius: 12px;
  }
  h1{
    color: #222;
    text-align: center;
    padding: 10px;
  }
  hr{
    color: purple;
  }
</style>



